/**
 * –ì–ª–∞–≤–Ω—ã–π –º–æ–¥—É–ª—å –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è
 * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ—Ç –≤—Å–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã —Å–∞–π—Ç–∞
 */

// –ò–º–ø–æ—Ä—Ç —Å—Ç–∏–ª–µ–π
import '../css/styles.css';

// –ò–º–ø–æ—Ä—Ç –¥–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã—Ö –º–æ–¥—É–ª–µ–π
import './mobile-menu.js';
import './simple-slider.js';
import './direct-footer-loader.js';

// –ò–º–ø–æ—Ä—Ç –º–æ–¥—É–ª–µ–π
import { Slider } from './modules/slider.js';
import { Navigation } from './modules/navigation.js';
import { Animations } from './modules/animations.js';
import { Portfolio } from './modules/portfolio.js';
import { Forms } from './modules/forms.js';
import { Performance, performanceOptimizations } from './modules/performance.js';
import { PhotoViewer } from './modules/photo-viewer.js';
import { Analytics } from './modules/analytics.js';
import { Components } from './modules/components.js';
import { PortfolioFilter } from './modules/portfolio-filter.js';
import { ModernLightbox } from './modules/modern-lightbox.js';
import { PortfolioIntegration } from './modules/portfolio-integration.js';
import { ScrollAnimations } from './modules/scroll-animations.js';
import { EnhancedForms } from './modules/enhanced-forms.js';
import { ProgressiveImageLoading } from './modules/progressive-images.js';
import { FAQAccordion } from './modules/faq-accordion.js';
import { devLog, isDevelopment } from './config.js';
import { TestimonialSlider } from './modules/testimonial-slider.js';

class App {
  constructor() {
    this.modules = {};
    this.isInitialized = false;

    devLog('App: –°–æ–∑–¥–∞–Ω–∏–µ —ç–∫–∑–µ–º–ø–ª—è—Ä–∞ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è');
    this.init();
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è
   */
  async init() {
    // –ñ–¥–µ–º –ø–æ–ª–Ω–æ–π –∑–∞–≥—Ä—É–∑–∫–∏ DOM
    if (document.readyState === 'loading') {
      document.addEventListener('DOMContentLoaded', () => {
        this.loadTemplatesAndInit();
      });
    } else {
      await this.loadTemplatesAndInit();
    }
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –±–µ–∑ —à–∞–±–ª–æ–Ω–æ–≤
   */
  async loadTemplatesAndInit() {
    try {
      // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º –º–æ–¥—É–ª–∏
      this.initModules();
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏:', error);
    }
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –≤—Å–µ—Ö –º–æ–¥—É–ª–µ–π
   */
  initModules() {
    try {
      const moduleConfigs = [
        // –ë–∞–∑–æ–≤—ã–µ —Å–∏—Å—Ç–µ–º–Ω—ã–µ –º–æ–¥—É–ª–∏ - –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É—é—Ç—Å—è –ø–µ—Ä–≤—ã–º–∏
        { name: 'performance', Class: Performance, description: '–û–ø—Ç–∏–º–∏–∑–∞—Ü–∏—è –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç–µ–ª—å–Ω–æ—Å—Ç–∏' },
        { name: 'analytics', Class: Analytics, description: '–ê–Ω–∞–ª–∏—Ç–∏–∫–∞' },
        
        // –ö–ª—é—á–µ–≤—ã–µ –º–æ–¥—É–ª–∏ UI
        { name: 'navigation', Class: Navigation, description: '–ù–∞–≤–∏–≥–∞—Ü–∏—è' },
        { name: 'progressiveImageLoading', Class: ProgressiveImageLoading, description: '–ü—Ä–æ–≥—Ä–µ—Å—Å–∏–≤–Ω–∞—è –∑–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π' },
        
        // –ö–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –∏ –∞–Ω–∏–º–∞—Ü–∏–∏
        { name: 'components', Class: Components, description: '–£–ª—É—á—à–µ–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤' },
        { name: 'animations', Class: Animations, description: '–ê–Ω–∏–º–∞—Ü–∏–∏' },
        { name: 'scrollAnimations', Class: ScrollAnimations, description: '–ê–Ω–∏–º–∞—Ü–∏–∏ –ø—Ä–æ–∫—Ä—É—Ç–∫–∏' },
        { name: 'slider', Class: Slider, description: '–°–ª–∞–π–¥–µ—Ä' },
        
        // –ü–æ—Ä—Ç—Ñ–æ–ª–∏–æ –∏ –≥–∞–ª–µ—Ä–µ—è
        { name: 'portfolio', Class: Portfolio, description: '–ü–æ—Ä—Ç—Ñ–æ–ª–∏–æ' },
        { name: 'portfolioFilter', Class: PortfolioFilter, description: '–§–∏–ª—å—Ç—Ä–∞—Ü–∏—è –ø–æ—Ä—Ç—Ñ–æ–ª–∏–æ' },
        { name: 'modernLightbox', Class: ModernLightbox, description: '–°–æ–≤—Ä–µ–º–µ–Ω–Ω—ã–π –ª–∞–π—Ç–±–æ–∫—Å' },
        { name: 'photoViewer', Class: PhotoViewer, description: '–ü—Ä–æ—Å–º–æ—Ç—Ä —Ñ–æ—Ç–æ–≥—Ä–∞—Ñ–∏–π' },
        { name: 'portfolioIntegration', Class: PortfolioIntegration, description: '–ò–Ω—Ç–µ–≥—Ä–∞—Ü–∏—è –ø–æ—Ä—Ç—Ñ–æ–ª–∏–æ' },
        
        // –§–æ—Ä–º—ã –∏ –∏–Ω—Ç–µ—Ä–∞–∫—Ç–∏–≤–Ω—ã–µ —ç–ª–µ–º–µ–Ω—Ç—ã
        { name: 'forms', Class: Forms, description: '–§–æ—Ä–º—ã' },
        { name: 'enhancedForms', Class: EnhancedForms, description: '–†–∞—Å—à–∏—Ä–µ–Ω–Ω—ã–µ —Ñ–æ—Ä–º—ã' },
        { name: 'faqAccordion', Class: FAQAccordion, description: 'FAQ –∞–∫–∫–æ—Ä–¥–µ–æ–Ω' },
        { name: 'testimonialSlider', Class: TestimonialSlider, description: '–°–ª–∞–π–¥–µ—Ä –æ—Ç–∑—ã–≤–æ–≤' },
      ];

      moduleConfigs.forEach(({ name, Class, description }) => {
        try {
          this.modules[name] = new Class();
          devLog(`‚úì ${description} –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä–æ–≤–∞–Ω`);
        } catch (error) {
          console.error(`‚ùå –û—à–∏–±–∫–∞ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏ ${description}:`, error);
        }
      });

      this.isInitialized = true;
      console.log('üöÄ –ü—Ä–∏–ª–æ–∂–µ–Ω–∏–µ –ø–æ–ª–Ω–æ—Å—Ç—å—é –∑–∞–≥—Ä—É–∂–µ–Ω–æ');

      // –ó–∞–ø—É—Å–∫–∞–µ–º –º–æ–Ω–∏—Ç–æ—Ä–∏–Ω–≥ –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç–µ–ª—å–Ω–æ—Å—Ç–∏
      if (this.modules.performance) {
        this.modules.performance.measurePerformance();
      }

      // –û—Ç—Å–ª–µ–∂–∏–≤–∞–µ–º —É—Å–ø–µ—à–Ω—É—é –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—é
      if (this.modules.analytics) {
        this.modules.analytics.trackEvent('app', 'initialized', 'success');
      }
    } catch (error) {
      console.error('‚ùå –ö—Ä–∏—Ç–∏—á–µ—Å–∫–∞—è –æ—à–∏–±–∫–∞ –ø—Ä–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏:', error);
      if (this.modules.analytics) {
        this.modules.analytics.trackEvent('app', 'initialization_error', error.message);
      }
    }
  }

  /**
   * –£–Ω–∏—á—Ç–æ–∂–µ–Ω–∏–µ –≤—Å–µ—Ö –º–æ–¥—É–ª–µ–π
   */
  destroy() {
    Object.entries(this.modules).forEach(([name, module]) => {
      if (module && typeof module.destroy === 'function') {
        try {
          module.destroy();
          devLog(`App: –ú–æ–¥—É–ª—å ${name} —É–Ω–∏—á—Ç–æ–∂–µ–Ω`);
        } catch (error) {
          console.error(`–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–Ω–∏—á—Ç–æ–∂–µ–Ω–∏–∏ –º–æ–¥—É–ª—è ${name}:`, error);
        }
      }
    });

    this.modules = {};
    this.isInitialized = false;
    console.log('üîÑ –ü—Ä–∏–ª–æ–∂–µ–Ω–∏–µ –æ—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–æ');
  }

  /**
   * –ü–µ—Ä–µ–∑–∞–ø—É—Å–∫ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è
   */
  restart() {
    devLog('App: –ü–µ—Ä–µ–∑–∞–ø—É—Å–∫ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è');
    this.destroy();
    setTimeout(() => {
      this.init();
    }, 100);
  }
}

/**
 * –õ–∞–π—Ç–±–æ–∫—Å –¥–ª—è —Ñ–æ—Ç–æ –Ω–∞ –≥–ª–∞–≤–Ω–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü–µ
 */
class HomeLightbox {
  constructor() {
    this.currentLightbox = null;
    this.init();
  }

  init() {
    this.setupPhotoLightbox();
    
    // –ó–∞–∫—Ä—ã—Ç–∏–µ –ª–∞–π—Ç–±–æ–∫—Å–∞ –ø–æ ESC
    document.addEventListener('keydown', (e) => {
      if (e.key === 'Escape') {
        this.closeLightbox();
      }
    });
  }

  setupPhotoLightbox() {
    const photoLightbox = document.querySelector('.about-home__photo-wrapper.photo-lightbox');
    if (!photoLightbox) return;

    photoLightbox.addEventListener('click', (e) => {
      e.preventDefault();
      this.openLightbox();
    });
  }

  openLightbox() {
    const photoImg = document.querySelector('.about-home__image');
    if (!photoImg) return;

    // –°–æ–∑–¥–∞–µ–º –ª–∞–π—Ç–±–æ–∫—Å
    const lightboxOverlay = document.createElement('div');
    lightboxOverlay.className = 'lightbox-overlay';
    lightboxOverlay.innerHTML = `
      <div class="lightbox-content">
        <div class="lightbox-loader">
          <div class="loader-spinner"></div>
        </div>
        <img src="${photoImg.src}" alt="${photoImg.alt}" class="lightbox-image" style="display: none;">
        <button class="lightbox-close" aria-label="–ó–∞–∫—Ä—ã—Ç—å">
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <line x1="18" y1="6" x2="6" y2="18"></line>
            <line x1="6" y1="6" x2="18" y2="18"></line>
          </svg>
        </button>
        <div class="lightbox-hint">
          <span>–ö–æ–ª—ë—Å–∏–∫–æ –º—ã—à–∏ ‚Äî –º–∞—Å—à—Ç–∞–± ‚Ä¢ –î–≤–æ–π–Ω–æ–π –∫–ª–∏–∫ ‚Äî —Å–±—Ä–æ—Å</span>
        </div>
      </div>
    `;

    // –î–æ–±–∞–≤–ª—è–µ–º –ª–∞–π—Ç–±–æ–∫—Å –≤ –¥–æ–∫—É–º–µ–Ω—Ç
    document.body.appendChild(lightboxOverlay);
    document.body.style.overflow = 'hidden';

    // –ê–Ω–∏–º–∞—Ü–∏—è –ø–æ—è–≤–ª–µ–Ω–∏—è
    requestAnimationFrame(() => {
      lightboxOverlay.classList.add('active');
    });

    // –û–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ –∑–∞–∫—Ä—ã—Ç–∏—è
    const closeBtn = lightboxOverlay.querySelector('.lightbox-close');
    const lightboxImage = lightboxOverlay.querySelector('.lightbox-image');
    const loader = lightboxOverlay.querySelector('.lightbox-loader');

    closeBtn.addEventListener('click', () => this.closeLightbox());
    
    lightboxOverlay.addEventListener('click', (e) => {
      if (e.target === lightboxOverlay) {
        this.closeLightbox();
      }
    });

    // –ó–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
    lightboxImage.onload = () => {
      loader.style.display = 'none';
      lightboxImage.style.display = 'block';
      // –ü–æ–∫–∞–∑—ã–≤–∞–µ–º –ø–æ–¥—Å–∫–∞–∑–∫—É –ø–æ—Å–ª–µ –∑–∞–≥—Ä—É–∑–∫–∏
      setTimeout(() => {
        lightboxOverlay.classList.add('show-hint');
      }, 500);
    };

    // –ü–æ–¥–¥–µ—Ä–∂–∫–∞ —Å–µ–Ω—Å–æ—Ä–Ω—ã—Ö –∂–µ—Å—Ç–æ–≤ –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö
    this.setupTouchGestures(lightboxOverlay);

    // –ü–æ–¥–¥–µ—Ä–∂–∫–∞ –º–∞—Å—à—Ç–∞–±–∏—Ä–æ–≤–∞–Ω–∏—è
    this.setupImageZoom(lightboxImage);

    // –°–æ—Ö—Ä–∞–Ω—è–µ–º —Å—Å—ã–ª–∫—É –Ω–∞ –ª–∞–π—Ç–±–æ–∫—Å
    this.currentLightbox = lightboxOverlay;
  }

  closeLightbox() {
    const lightbox = this.currentLightbox || document.querySelector('.lightbox-overlay');
    if (!lightbox) return;

    const lightboxImage = lightbox.querySelector('.lightbox-image');
    
    // –°–±—Ä–∞—Å—ã–≤–∞–µ–º –≤—Å–µ —Å—Ç–∏–ª–∏
    if (lightboxImage) {
      lightboxImage.style.transform = '';
      lightboxImage.style.transition = '';
    }
    lightbox.style.background = '';
    lightbox.style.opacity = '';

    lightbox.classList.remove('active');
    document.body.style.overflow = '';

    // –£–¥–∞–ª—è–µ–º —ç–ª–µ–º–µ–Ω—Ç –ø–æ—Å–ª–µ –∞–Ω–∏–º–∞—Ü–∏–∏
    setTimeout(() => {
      if (lightbox.parentNode) {
        lightbox.parentNode.removeChild(lightbox);
      }
      this.currentLightbox = null;
    }, 300);
  }

  setupTouchGestures(lightboxOverlay) {
    let touchStartY = 0;
    let touchStartX = 0;
    let isDragging = false;
    let draggedElement = null;
    let initialImageTransform = '';

    const lightboxImage = lightboxOverlay.querySelector('.lightbox-image');

    // Touch start - –æ–ø—Ä–µ–¥–µ–ª—è–µ–º –Ω–∞—á–∞–ª–æ –∫–∞—Å–∞–Ω–∏—è
    lightboxOverlay.addEventListener('touchstart', (e) => {
      if (e.touches.length === 1) {
        touchStartY = e.touches[0].clientY;
        touchStartX = e.touches[0].clientX;
        isDragging = true;
        draggedElement = e.target;
        
        // –°–æ—Ö—Ä–∞–Ω—è–µ–º –Ω–∞—á–∞–ª—å–Ω—ã–π transform –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
        initialImageTransform = lightboxImage.style.transform || '';
      }
    }, { passive: true });

    // Touch move - –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º –¥–≤–∏–∂–µ–Ω–∏–µ
    lightboxOverlay.addEventListener('touchmove', (e) => {
      if (!isDragging || e.touches.length !== 1) return;

      const touchY = e.touches[0].clientY;
      const touchX = e.touches[0].clientX;
      const deltaY = touchY - touchStartY;
      const deltaX = touchX - touchStartX;

      // –ï—Å–ª–∏ –¥–≤–∏–∂–µ–Ω–∏–µ –ø—Ä–µ–∏–º—É—â–µ—Å—Ç–≤–µ–Ω–Ω–æ –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–µ
      if (Math.abs(deltaY) > Math.abs(deltaX) && Math.abs(deltaY) > 30) {
        e.preventDefault();
        
        // –í—ã—á–∏—Å–ª—è–µ–º –ø—Ä–æ–∑—Ä–∞—á–Ω–æ—Å—Ç—å overlay
        const opacity = Math.max(0.3, 1 - Math.abs(deltaY) / 300);
        lightboxOverlay.style.background = `rgba(0, 0, 0, ${opacity * 0.9})`;
        
        // –ü–µ—Ä–µ–º–µ—â–∞–µ–º –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –≤–º–µ—Å—Ç–µ —Å –ø–∞–ª—å—Ü–µ–º
        const scale = Math.max(0.7, 1 - Math.abs(deltaY) / 800);
        const translateY = deltaY * 0.8;
        
        lightboxImage.style.transform = `${initialImageTransform} translateY(${translateY}px) scale(${scale})`;
        lightboxImage.style.transition = 'none';
      }
    }, { passive: false });

    // Touch end - –∑–∞–≤–µ—Ä—à–µ–Ω–∏–µ –∫–∞—Å–∞–Ω–∏—è
    lightboxOverlay.addEventListener('touchend', (e) => {
      if (!isDragging) return;
      isDragging = false;

      const touchEndY = e.changedTouches[0].clientY;
      const touchEndX = e.changedTouches[0].clientX;
      const deltaY = touchEndY - touchStartY;
      const deltaX = touchEndX - touchStartX;

      // –í–æ—Å—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –ø–µ—Ä–µ—Ö–æ–¥—ã
      lightboxImage.style.transition = 'transform 0.3s ease';

      // –ï—Å–ª–∏ —Å–≤–∞–π–ø –±–æ–ª—å—à–µ 120px –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ - –∑–∞–∫—Ä—ã–≤–∞–µ–º
      if (Math.abs(deltaY) > 120 && Math.abs(deltaY) > Math.abs(deltaX)) {
        this.closeLightbox();
      } else if (Math.abs(deltaY) < 30 && Math.abs(deltaX) < 30) {
        // –ï—Å–ª–∏ —ç—Ç–æ –ø—Ä–æ—Å—Ç–æ —Ç–∞–ø –±–µ–∑ –¥–≤–∏–∂–µ–Ω–∏—è
        if (draggedElement === lightboxOverlay) {
          // –¢–∞–ø –ø–æ –æ–±–ª–∞—Å—Ç–∏ –∑–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ–º - –∑–∞–∫—Ä—ã–≤–∞–µ–º
          this.closeLightbox();
        } else if (draggedElement === lightboxImage) {
          // –¢–∞–ø –ø–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—é - —Ç–æ–∂–µ –∑–∞–∫—Ä—ã–≤–∞–µ–º (–∫–∞–∫ –∞–ª—å—Ç–µ—Ä–Ω–∞—Ç–∏–≤–∞ –∫–Ω–æ–ø–∫–µ)
          this.closeLightbox();
        }
      } else {
        // –í–æ–∑–≤—Ä–∞—â–∞–µ–º –≤ –∏—Å—Ö–æ–¥–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ
        lightboxOverlay.style.background = '';
        lightboxImage.style.transform = initialImageTransform;
      }
    }, { passive: true });

    // Touch cancel - –æ—Ç–º–µ–Ω–∞ –∫–∞—Å–∞–Ω–∏—è
    lightboxOverlay.addEventListener('touchcancel', () => {
      if (isDragging) {
        isDragging = false;
        lightboxOverlay.style.background = '';
        lightboxImage.style.transform = initialImageTransform;
        lightboxImage.style.transition = 'transform 0.3s ease';
      }
    }, { passive: true });
  }

  setupImageZoom(image) {
    let scale = 1;
    let isDragging = false;
    let startX = 0;
    let startY = 0;
    let translateX = 0;
    let translateY = 0;

    // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è transform
    const updateTransform = () => {
      if (scale === 1 && translateX === 0 && translateY === 0) {
        // –£–±–∏—Ä–∞–µ–º inline —Å—Ç–∏–ª–∏, –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—Å—è –∫ CSS
        image.style.transform = '';
      } else {
        image.style.transform = `scale(${scale}) translate(${translateX}px, ${translateY}px)`;
      }
    };

    // –ú–∞—Å—à—Ç–∞–±–∏—Ä–æ–≤–∞–Ω–∏–µ –∫–æ–ª—ë—Å–∏–∫–æ–º –º—ã—à–∏
    image.addEventListener('wheel', (e) => {
      e.preventDefault();
      
      const delta = e.deltaY > 0 ? -0.1 : 0.1;
      scale = Math.max(1, Math.min(3, scale + delta));
      
      // –°–±—Ä–æ—Å –ø–æ–∑–∏—Ü–∏–∏ –ø—Ä–∏ –º–∏–Ω–∏–º–∞–ª—å–Ω–æ–º –º–∞—Å—à—Ç–∞–±–µ
      if (scale === 1) {
        translateX = 0;
        translateY = 0;
      }
      
      updateTransform();
    });

    // –ü–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏–µ –ø—Ä–∏ —É–≤–µ–ª–∏—á–µ–Ω–∏–∏
    image.addEventListener('mousedown', (e) => {
      if (scale > 1) {
        isDragging = true;
        startX = e.clientX - translateX;
        startY = e.clientY - translateY;
        image.style.cursor = 'grabbing';
        e.preventDefault();
      }
    });

    document.addEventListener('mousemove', (e) => {
      if (isDragging && scale > 1) {
        translateX = e.clientX - startX;
        translateY = e.clientY - startY;
        updateTransform();
      }
    });

    document.addEventListener('mouseup', () => {
      if (isDragging) {
        isDragging = false;
        image.style.cursor = scale > 1 ? 'grab' : 'default';
      }
    });

    // –î–≤–æ–π–Ω–æ–π –∫–ª–∏–∫ –¥–ª—è —Å–±—Ä–æ—Å–∞ –º–∞—Å—à—Ç–∞–±–∞
    image.addEventListener('dblclick', () => {
      scale = 1;
      translateX = 0;
      translateY = 0;
      updateTransform();
      image.style.cursor = 'default';
    });

    // –ü–æ–¥–¥–µ—Ä–∂–∫–∞ —â–∏–ø–∫–∞ –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö
    this.setupTouchZoom(image);

    // –ù–∞—á–∞–ª—å–Ω—ã–π –∫—É—Ä—Å–æ—Ä
    image.style.cursor = 'default';
  }

  setupTouchZoom(image) {
    let initialDistance = 0;
    let scale = 1;

    // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è transform –≤ –º–æ–±–∏–ª—å–Ω–æ–π –≤–µ—Ä—Å–∏–∏
    const updateMobileTransform = () => {
      if (scale === 1) {
        image.style.transform = '';
      } else {
        image.style.transform = `scale(${scale})`;
      }
    };

    image.addEventListener('touchstart', (e) => {
      if (e.touches.length === 2) {
        e.preventDefault();
        initialDistance = this.getDistance(e.touches[0], e.touches[1]);
      }
    });

    image.addEventListener('touchmove', (e) => {
      if (e.touches.length === 2) {
        e.preventDefault();
        const currentDistance = this.getDistance(e.touches[0], e.touches[1]);
        const deltaScale = currentDistance / initialDistance;
        scale = Math.max(1, Math.min(3, scale * deltaScale));
        
        updateMobileTransform();
        initialDistance = currentDistance;
      }
    });

    image.addEventListener('touchend', (e) => {
      if (e.touches.length < 2) {
        // –°–±—Ä–æ—Å –ø—Ä–∏ –º–∏–Ω–∏–º–∞–ª—å–Ω–æ–º –º–∞—Å—à—Ç–∞–±–µ
        if (scale <= 1.1) {
          scale = 1;
          updateMobileTransform();
        }
      }
    });
  }

  getDistance(touch1, touch2) {
    const dx = touch1.clientX - touch2.clientX;
    const dy = touch1.clientY - touch2.clientY;
    return Math.sqrt(dx * dx + dy * dy);
  }
}

