/**
 * –ú–æ–¥—É–ª—å FAQ –∞–∫–∫–æ—Ä–¥–µ–æ–Ω–∞
 */

import { devLog } from '../config.js';

export class FAQAccordion {
    constructor() {
        devLog('FAQAccordion: –ù–∞—á–∞–ª–æ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏');
        
        // –ò—Å–ø–æ–ª—å–∑—É–µ–º setTimeout –¥–ª—è –≥–∞—Ä–∞–Ω—Ç–∏–∏, —á—Ç–æ DOM –ø–æ–ª–Ω–æ—Å—Ç—å—é –∑–∞–≥—Ä—É–∂–µ–Ω
        setTimeout(() => {
            this.faqItems = document.querySelectorAll('.faq-item');
            devLog(`FAQAccordion: –ù–∞–π–¥–µ–Ω–æ ${this.faqItems.length} FAQ —ç–ª–µ–º–µ–Ω—Ç–æ–≤`);
            
            if (this.faqItems.length === 0) {
                devLog('FAQAccordion: FAQ —ç–ª–µ–º–µ–Ω—Ç—ã –Ω–µ –Ω–∞–π–¥–µ–Ω—ã, –ø–æ–≤—Ç–æ—Ä–Ω–∞—è –ø–æ–ø—ã—Ç–∫–∞ —á–µ—Ä–µ–∑ 1 —Å–µ–∫—É–Ω–¥—É');
                setTimeout(() => {
                    this.faqItems = document.querySelectorAll('.faq-item');
                    devLog(`FAQAccordion: –ü–æ–≤—Ç–æ—Ä–Ω–∞—è –ø–æ–ø—ã—Ç–∫–∞ - –Ω–∞–π–¥–µ–Ω–æ ${this.faqItems.length} FAQ —ç–ª–µ–º–µ–Ω—Ç–æ–≤`);
                    this.init();
                }, 1000);
            } else {
                this.init();
            }
        }, 100);
    }

    init() {
        if (this.faqItems.length === 0) {
            devLog('FAQAccordion: FAQ —ç–ª–µ–º–µ–Ω—Ç—ã –Ω–µ –Ω–∞–π–¥–µ–Ω—ã');
            return;
        }

        this.bindEvents();
        devLog(`FAQAccordion: –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä–æ–≤–∞–Ω–æ ${this.faqItems.length} FAQ —ç–ª–µ–º–µ–Ω—Ç–æ–≤`);

        // –ì–ª–æ–±–∞–ª—å–Ω–∞—è —Å—Å—ã–ª–∫–∞ –¥–ª—è –æ—Ç–ª–∞–¥–∫–∏
        window.faqDebug = this;
    }

    bindEvents() {
        this.faqItems.forEach((item, index) => {
            const trigger = item.querySelector('.faq-item__trigger');
            if (trigger) {
                devLog(`FAQAccordion: –ü—Ä–∏–≤—è–∑–∫–∞ —Å–æ–±—ã—Ç–∏–π –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–∞ ${index}`);
                
                // –ü—Ä–æ—Å—Ç–∞—è –æ–±—Ä–∞–±–æ—Ç–∫–∞ –∫–ª–∏–∫–∞
                trigger.addEventListener('click', (e) => {
                    e.preventDefault();
                    e.stopPropagation();
                    devLog(`FAQAccordion: –ö–ª–∏–∫ –ø–æ —ç–ª–µ–º–µ–Ω—Ç—É ${index}`);
                    this.toggleItem(item);
                });
                
                trigger.addEventListener('keydown', (e) => this.handleKeydown(e, item));
                
                // –£—Å—Ç–∞–Ω–æ–≤–∫–∞ ARIA –∞—Ç—Ä–∏–±—É—Ç–æ–≤
                trigger.setAttribute('aria-expanded', 'false');
                trigger.setAttribute('aria-controls', `faq-content-${index}`);
                
                const content = item.querySelector('.faq-item__content');
                if (content) {
                    content.setAttribute('id', `faq-content-${index}`);
                    content.setAttribute('aria-hidden', 'true');
                    // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –Ω–∞—á–∞–ª—å–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ
                    content.style.maxHeight = '0px';
                    devLog(`FAQAccordion: –ö–æ–Ω—Ç–µ–Ω—Ç –Ω–∞–π–¥–µ–Ω –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–∞ ${index}`);
                } else {
                    devLog(`FAQAccordion: –ö–æ–Ω—Ç–µ–Ω—Ç –ù–ï –Ω–∞–π–¥–µ–Ω –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–∞ ${index}`);
                }
            } else {
                devLog(`FAQAccordion: –¢—Ä–∏–≥–≥–µ—Ä –ù–ï –Ω–∞–π–¥–µ–Ω –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–∞ ${index}`);
            }
        });
    }

    toggleItem(item) {
        if (!item) {
            devLog('FAQAccordion: –≠–ª–µ–º–µ–Ω—Ç –Ω–µ –ø–µ—Ä–µ–¥–∞–Ω –≤ toggleItem');
            return;
        }

        const trigger = item.querySelector('.faq-item__trigger');
        const content = item.querySelector('.faq-item__content');
        const isActive = item.classList.contains('active');

        devLog(`FAQAccordion: toggleItem - —ç–ª–µ–º–µ–Ω—Ç ${isActive ? '–∞–∫—Ç–∏–≤–µ–Ω' : '–Ω–µ–∞–∫—Ç–∏–≤–µ–Ω'}`);

        if (isActive) {
            this.closeItem(item);
        } else {
            // –ó–∞–∫—Ä—ã–≤–∞–µ–º –≤—Å–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ —ç–ª–µ–º–µ–Ω—Ç—ã
            this.faqItems.forEach(otherItem => {
                if (otherItem !== item && otherItem.classList.contains('active')) {
                    this.closeItem(otherItem);
                }
            });
            
            // –û—Ç–∫—Ä—ã–≤–∞–µ–º —Ç–µ–∫—É—â–∏–π —ç–ª–µ–º–µ–Ω—Ç
            this.openItem(item);
        }
    }

    openItem(item) {
        const trigger = item.querySelector('.faq-item__trigger');
        const content = item.querySelector('.faq-item__content');
        
        if (!content) {
            devLog('FAQAccordion: –ö–æ–Ω—Ç–µ–Ω—Ç –Ω–µ –Ω–∞–π–¥–µ–Ω –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è');
            return;
        }
        
        item.classList.add('active');
        
        if (trigger) {
            trigger.setAttribute('aria-expanded', 'true');
        }
        
        if (content) {
            content.setAttribute('aria-hidden', 'false');
            
            // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º max-height –¥–ª—è –ø–ª–∞–≤–Ω–æ–π –∞–Ω–∏–º–∞—Ü–∏–∏
            const scrollHeight = content.scrollHeight;
            content.style.maxHeight = scrollHeight + 'px';
            
            devLog(`FAQAccordion: –≠–ª–µ–º–µ–Ω—Ç –æ—Ç–∫—Ä—ã—Ç, –≤—ã—Å–æ—Ç–∞: ${scrollHeight}px`);
        }
    }

    closeItem(item) {
        const trigger = item.querySelector('.faq-item__trigger');
        const content = item.querySelector('.faq-item__content');
        
        if (!content) {
            devLog('FAQAccordion: –ö–æ–Ω—Ç–µ–Ω—Ç –Ω–µ –Ω–∞–π–¥–µ–Ω –¥–ª—è –∑–∞–∫—Ä—ã—Ç–∏—è');
            return;
        }
        
        item.classList.remove('active');
        
        if (trigger) {
            trigger.setAttribute('aria-expanded', 'false');
        }
        
        if (content) {
            content.setAttribute('aria-hidden', 'true');
            content.style.maxHeight = '0px';
            
            devLog('FAQAccordion: –≠–ª–µ–º–µ–Ω—Ç –∑–∞–∫—Ä—ã—Ç');
        }
    }

    handleKeydown(e, item) {
        if (e.key === 'Enter' || e.key === ' ') {
            e.preventDefault();
            this.toggleItem(item);
        }
    }

    // –ú–µ—Ç–æ–¥ –¥–ª—è –ø—Ä–æ–≥—Ä–∞–º–º–Ω–æ–≥–æ –æ—Ç–∫—Ä—ã—Ç–∏—è/–∑–∞–∫—Ä—ã—Ç–∏—è
    toggleItemByIndex(index) {
        if (this.faqItems[index]) {
            this.toggleItem(this.faqItems[index]);
        }
    }

    // –ú–µ—Ç–æ–¥ –¥–ª—è –∑–∞–∫—Ä—ã—Ç–∏—è –≤—Å–µ—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤
    closeAll() {
        this.faqItems.forEach(item => {
            if (item.classList.contains('active')) {
                this.closeItem(item);
            }
        });
    }

    // –ú–µ—Ç–æ–¥ –¥–ª—è –æ—Ç–ª–∞–¥–∫–∏ —Å–æ—Å—Ç–æ—è–Ω–∏—è FAQ
    debugState() {
        console.log('FAQ Debug State:');
        this.faqItems.forEach((item, index) => {
            const trigger = item.querySelector('.faq-item__trigger');
            const content = item.querySelector('.faq-item__content');
            const isActive = item.classList.contains('active');
            
            console.log(`FAQ Item ${index}:`, {
                isActive,
                triggerExists: !!trigger,
                contentExists: !!content,
                contentMaxHeight: content ? content.style.maxHeight : 'none',
                ariaExpanded: trigger ? trigger.getAttribute('aria-expanded') : 'none'
            });
        });
    }
}
