/**
 * –ü—Ä–æ—Å—Ç–æ–π –∏ –ø—Ä—è–º–æ–π –∑–∞–≥—Ä—É–∑—á–∏–∫ —Ñ—É—Ç–µ—Ä–∞ –±–µ–∑ —Å–ª–æ–∂–Ω–æ–π –ª–æ–≥–∏–∫–∏
 */

console.log('üöÄ –ó–∞–≥—Ä—É–∂–∞–µ—Ç—Å—è direct-footer-loader.js');

// –ü—Ä–æ—Å—Ç–∞—è —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
function openModal(modalId) {
    console.log('üîì –ü—ã—Ç–∞–µ–º—Å—è –æ—Ç–∫—Ä—ã—Ç—å –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ:', modalId);
    
    // –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –¥–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞
    const allModals = document.querySelectorAll('.legal-modal');
    console.log(`üîç –í—Å–µ–≥–æ –º–æ–¥–∞–ª—å–Ω—ã—Ö –æ–∫–æ–Ω —Å –∫–ª–∞—Å—Å–æ–º .legal-modal: ${allModals.length}`);
    allModals.forEach((modal, i) => {
        console.log(`  ${i+1}. ID: ${modal.id}, display: ${modal.style.display || '–Ω–µ –∑–∞–¥–∞–Ω'}`);
    });
    
    const modal = document.getElementById(modalId);
    if (modal) {
        console.log('‚úÖ –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –Ω–∞–π–¥–µ–Ω–æ:', modalId);
        modal.style.display = 'block';
        modal.classList.add('show');
        document.body.style.overflow = 'hidden';
        
        const body = modal.querySelector('.legal-modal__body');
        if (body) body.scrollTop = 0;
        
        console.log('‚úÖ –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –æ—Ç–∫—Ä—ã—Ç–æ');
    } else {
        console.error('‚ùå –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ:', modalId);
        
        // –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –¥–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞
        console.log('üîç –ü–æ–∏—Å–∫ –ø–æ –≤—Å–µ–º —ç–ª–µ–º–µ–Ω—Ç–∞–º —Å ID:');
        const allElements = document.querySelectorAll('[id]');
        const relevantElements = Array.from(allElements).filter(el => 
            el.id.includes('modal') || el.id.includes('rights')
        );
        relevantElements.forEach(el => {
            console.log(`  –ù–∞–π–¥–µ–Ω —ç–ª–µ–º–µ–Ω—Ç: ${el.id} (${el.tagName})`);
        });
    }
}

// –ü—Ä–æ—Å—Ç–∞—è —Ñ—É–Ω–∫—Ü–∏—è –¥–ª—è –∑–∞–∫—Ä—ã—Ç–∏—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞
function closeModal(modalId) {
    console.log('üîí –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ:', modalId);
    const modal = document.getElementById(modalId);
    if (modal) {
        modal.style.display = 'none';
        modal.classList.remove('show');
        document.body.style.overflow = 'auto';
        console.log('‚úÖ –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –∑–∞–∫—Ä—ã—Ç–æ');
    }
}

// –î–æ–±–∞–≤–ª—è–µ–º —Ñ—É–Ω–∫—Ü–∏–∏ –≤ –≥–ª–æ–±–∞–ª—å–Ω—ã–π –æ–±—ä–µ–∫—Ç
window.openModal = openModal;
window.closeModal = closeModal;

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –Ω–∞—Å—Ç—Ä–æ–π–∫–∏ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤
function setupModals() {
    console.log('‚öôÔ∏è –ù–∞—Å—Ç—Ä–æ–π–∫–∞ –º–æ–¥–∞–ª—å–Ω—ã—Ö –æ–∫–æ–Ω...');
    
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º –Ω–∞–ª–∏—á–∏–µ –≤—Å–µ—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Ñ—É—Ç–µ—Ä–∞
    const footerElement = document.querySelector('footer');
    const footerPlaceholder = document.getElementById('footer-placeholder');
    
    console.log('üîç –ü—Ä–æ–≤–µ—Ä–∫–∞ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ —Ñ—É—Ç–µ—Ä–∞:');
    console.log('  footer —ç–ª–µ–º–µ–Ω—Ç:', footerElement ? '–Ω–∞–π–¥–µ–Ω' : '–Ω–µ –Ω–∞–π–¥–µ–Ω');
    console.log('  footer-placeholder:', footerPlaceholder ? '–Ω–∞–π–¥–µ–Ω' : '–Ω–µ –Ω–∞–π–¥–µ–Ω');
    
    if (footerPlaceholder) {
        console.log('  –°–æ–¥–µ—Ä–∂–∏–º–æ–µ placeholder:', footerPlaceholder.innerHTML.length, '—Å–∏–º–≤–æ–ª–æ–≤');
    }
    
    // –°—Å—ã–ª–∫–∏ –¥–ª—è –æ—Ç–∫—Ä—ã—Ç–∏—è
    const links = {
        'privacy-link': 'privacy-modal',
        'terms-link': 'terms-modal', 
        'rights-link': 'rights-modal'
    };
    
    console.log('üîó –ü—Ä–æ–≤–µ—Ä–∫–∞ —Å—Å—ã–ª–æ–∫ –∏ –º–æ–¥–∞–ª—å–Ω—ã—Ö –æ–∫–æ–Ω:');
    
    // –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –¥–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞ DOM
    const allModals = document.querySelectorAll('.legal-modal');
    const allElementsWithModal = document.querySelectorAll('[id*="modal"]');
    
    console.log(`üîç –î–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞ DOM:`);
    console.log(`  –≠–ª–µ–º–µ–Ω—Ç–æ–≤ —Å –∫–ª–∞—Å—Å–æ–º .legal-modal: ${allModals.length}`);
    console.log(`  –≠–ª–µ–º–µ–Ω—Ç–æ–≤ —Å "modal" –≤ ID: ${allElementsWithModal.length}`);
    
    allModals.forEach((modal, i) => {
        console.log(`    ${i+1}. –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ: ${modal.id} (–∫–ª–∞—Å—Å: ${modal.className})`);
    });
    
    allElementsWithModal.forEach((element, i) => {
        if (!element.classList.contains('legal-modal')) {
            console.log(`    –î—Ä—É–≥–æ–π —ç–ª–µ–º–µ–Ω—Ç —Å modal: ${element.id} (${element.tagName})`);
        }
    });
    
    Object.entries(links).forEach(([linkId, modalId]) => {
        const link = document.getElementById(linkId);
        const modal = document.getElementById(modalId);
        
        console.log(`  ${linkId}: ${link ? '‚úÖ –Ω–∞–π–¥–µ–Ω' : '‚ùå –Ω–µ –Ω–∞–π–¥–µ–Ω'}`);
        console.log(`  ${modalId}: ${modal ? '‚úÖ –Ω–∞–π–¥–µ–Ω' : '‚ùå –Ω–µ –Ω–∞–π–¥–µ–Ω'}`);
        
        if (link && modal) {
            console.log(`‚úÖ –ù–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º ${linkId} -> ${modalId}`);
            link.addEventListener('click', function(e) {
                e.preventDefault();
                console.log(`üñ±Ô∏è –ö–ª–∏–∫ –ø–æ ${linkId}`);
                openModal(modalId);
                return false;
            });
        } else {
            if (!link) console.error(`‚ùå –°—Å—ã–ª–∫–∞ –Ω–µ –Ω–∞–π–¥–µ–Ω–∞: ${linkId}`);
            if (!modal) console.error(`‚ùå –ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ: ${modalId}`);
        }
    });
    
    // –ö–Ω–æ–ø–∫–∏ –∑–∞–∫—Ä—ã—Ç–∏—è
    const closeButtons = document.querySelectorAll('.legal-modal__close');
    console.log(`üîò –ù–∞–π–¥–µ–Ω–æ –∫–Ω–æ–ø–æ–∫ –∑–∞–∫—Ä—ã—Ç–∏—è: ${closeButtons.length}`);
    
    closeButtons.forEach(button => {
        button.addEventListener('click', function() {
            const modal = this.closest('.legal-modal');
            if (modal) {
                closeModal(modal.id);
            }
        });
    });
    
    // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –Ω–∞ —Ñ–æ–Ω
    document.addEventListener('click', function(event) {
        if (event.target.classList.contains('legal-modal')) {
            closeModal(event.target.id);
        }
    });
    
    // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ Escape
    document.addEventListener('keydown', function(event) {
        if (event.key === 'Escape') {
            const openModals = document.querySelectorAll('.legal-modal.show');
            openModals.forEach(modal => closeModal(modal.id));
        }
    });
    
    console.log('‚úÖ –ú–æ–¥–∞–ª—å–Ω—ã–µ –æ–∫–Ω–∞ –Ω–∞—Å—Ç—Ä–æ–µ–Ω—ã');
}

// –ó–∞–≥—Ä—É–∑–∫–∞ —Ñ—É—Ç–µ—Ä–∞
async function loadFooter() {
    try {
        console.log('üì• –ó–∞–≥—Ä—É–∂–∞–µ–º —Ñ—É—Ç–µ—Ä...');
        
        // –û–ø—Ä–µ–¥–µ–ª—è–µ–º –ø—É—Ç—å –∫ —Ñ—É—Ç–µ—Ä—É
        const currentPath = window.location.pathname;
        let footerPath = 'parts/_footer.html';
        
        if (currentPath.includes('/pages/')) {
            footerPath = '../../parts/_footer.html';
        } else if (currentPath.includes('/gallery/')) {
            footerPath = '../../../parts/_footer.html';
        }
        
        console.log('üìÇ –ü—É—Ç—å –∫ —Ñ—É—Ç–µ—Ä—É:', footerPath);
        
        const response = await fetch(footerPath);
        if (!response.ok) {
            throw new Error(`HTTP ${response.status}: ${response.statusText}`);
        }
        
        const footerHTML = await response.text();
        console.log('üìÑ –§—É—Ç–µ—Ä –∑–∞–≥—Ä—É–∂–µ–Ω, —Ä–∞–∑–º–µ—Ä:', footerHTML.length, '—Å–∏–º–≤–æ–ª–æ–≤');
        
        const placeholder = document.getElementById('footer-placeholder');
        if (!placeholder) {
            console.error('‚ùå –ù–µ –Ω–∞–π–¥–µ–Ω footer-placeholder');
            return;
        }
        
        placeholder.innerHTML = footerHTML;
        console.log('‚úÖ –§—É—Ç–µ—Ä –≤—Å—Ç–∞–≤–ª–µ–Ω –≤ DOM');
        
        // –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è –ø—Ä–æ–≤–µ—Ä–∫–∞ —Å–æ–¥–µ—Ä–∂–∏–º–æ–≥–æ
        console.log('üîç –ü—Ä–æ–≤–µ—Ä–∫–∞ —Å–æ–¥–µ—Ä–∂–∏–º–æ–≥–æ —Ñ—É—Ç–µ—Ä–∞:');
        console.log('  –°–æ–¥–µ—Ä–∂–∏—Ç "rights-modal":', footerHTML.includes('rights-modal'));
        console.log('  –°–æ–¥–µ—Ä–∂–∏—Ç "privacy-modal":', footerHTML.includes('privacy-modal'));
        console.log('  –°–æ–¥–µ—Ä–∂–∏—Ç "terms-modal":', footerHTML.includes('terms-modal'));
        console.log('  –î–ª–∏–Ω–∞ HTML:', footerHTML.length);
        
        // –ü—Ä–æ–≤–µ—Ä–∫–∞ –ø–æ—Å–ª–µ –≤—Å—Ç–∞–≤–∫–∏ –≤ DOM
        setTimeout(() => {
            const rightsModalInDOM = document.getElementById('rights-modal');
            console.log('üîç rights-modal –≤ DOM —á–µ—Ä–µ–∑ 50–º—Å:', rightsModalInDOM ? '–Ω–∞–π–¥–µ–Ω' : '–ù–ï –ù–ê–ô–î–ï–ù');
            if (rightsModalInDOM) {
                console.log('  –ö–ª–∞—Å—Å:', rightsModalInDOM.className);
                console.log('  –†–æ–¥–∏—Ç–µ–ª—å:', rightsModalInDOM.parentElement?.tagName);
            }
        }, 50);
        
        // –ù–∞—Å—Ç—Ä–æ–π–∫–∞ –º–æ–¥–∞–ª—å–Ω—ã—Ö –æ–∫–æ–Ω —Å –Ω–µ—Å–∫–æ–ª—å–∫–∏–º–∏ –ø–æ–ø—ã—Ç–∫–∞–º–∏
        let attempts = 0;
        const maxAttempts = 5;
        
        function trySetupModals() {
            attempts++;
            console.log(`üîß –ü–æ–ø—ã—Ç–∫–∞ –Ω–∞—Å—Ç—Ä–æ–π–∫–∏ –º–æ–¥–∞–ª—å–Ω—ã—Ö –æ–∫–æ–Ω ${attempts}/${maxAttempts}...`);
            
            const termsModal = document.getElementById('terms-modal');
            const privacyModal = document.getElementById('privacy-modal');
            const rightsModal = document.getElementById('rights-modal');
            
            console.log('üìä –°–æ—Å—Ç–æ—è–Ω–∏–µ –º–æ–¥–∞–ª—å–Ω—ã—Ö –æ–∫–æ–Ω:');
            console.log(`  terms-modal: ${termsModal ? '–Ω–∞–π–¥–µ–Ω' : '–ù–ï –ù–ê–ô–î–ï–ù'}`);
            console.log(`  privacy-modal: ${privacyModal ? '–Ω–∞–π–¥–µ–Ω' : '–ù–ï –ù–ê–ô–î–ï–ù'}`);
            console.log(`  rights-modal: ${rightsModal ? '–Ω–∞–π–¥–µ–Ω' : '–ù–ï –ù–ê–ô–î–ï–ù'}`);
            
            // –ï—Å–ª–∏ –≤—Å–µ –º–æ–¥–∞–ª—å–Ω—ã–µ –æ–∫–Ω–∞ –Ω–∞–π–¥–µ–Ω—ã, –Ω–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º –∏—Ö
            if (termsModal && privacyModal && rightsModal) {
                console.log('‚úÖ –í—Å–µ –º–æ–¥–∞–ª—å–Ω—ã–µ –æ–∫–Ω–∞ –Ω–∞–π–¥–µ–Ω—ã, –Ω–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏...');
                setupModals();
                return;
            }
            
            // –ï—Å–ª–∏ –Ω–µ –≤—Å–µ –Ω–∞–π–¥–µ–Ω—ã –∏ –µ—Å—Ç—å –µ—â—ë –ø–æ–ø—ã—Ç–∫–∏, –ø—Ä–æ–±—É–µ–º —Å–Ω–æ–≤–∞
            if (attempts < maxAttempts) {
                console.log(`‚è≥ –ù–µ –≤—Å–µ –º–æ–¥–∞–ª—å–Ω—ã–µ –æ–∫–Ω–∞ –Ω–∞–π–¥–µ–Ω—ã, –ø–æ–≤—Ç–æ—Ä–Ω–∞—è –ø–æ–ø—ã—Ç–∫–∞ —á–µ—Ä–µ–∑ ${200 * attempts}–º—Å...`);
                setTimeout(trySetupModals, 200 * attempts);
            } else {
                console.error('‚ùå –ù–µ —É–¥–∞–ª–æ—Å—å –Ω–∞–π—Ç–∏ –≤—Å–µ –º–æ–¥–∞–ª—å–Ω—ã–µ –æ–∫–Ω–∞ –ø–æ—Å–ª–µ –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–≥–æ –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ –ø–æ–ø—ã—Ç–æ–∫');
                
                // –í—Å—ë —Ä–∞–≤–Ω–æ –ø—ã—Ç–∞–µ–º—Å—è –Ω–∞—Å—Ç—Ä–æ–∏—Ç—å —Ç–æ, —á—Ç–æ –µ—Å—Ç—å
                console.log('üîß –ù–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º –Ω–∞–π–¥–µ–Ω–Ω—ã–µ –º–æ–¥–∞–ª—å–Ω—ã–µ –æ–∫–Ω–∞...');
                setupModals();
            }
        }
        
        // –ù–∞—á–∏–Ω–∞–µ–º –ø–æ–ø—ã—Ç–∫–∏ –Ω–∞—Å—Ç—Ä–æ–π–∫–∏
        setTimeout(trySetupModals, 100);
        
    } catch (error) {
        console.error('‚ùå –û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏ —Ñ—É—Ç–µ—Ä–∞:', error);
    }
}

// –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ DOM
if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', loadFooter);
} else {
    loadFooter();
}

console.log('üìã direct-footer-loader.js –≥–æ—Ç–æ–≤');
