/**
 * –ú–æ–¥—É–ª—å —Å–ª–∞–π–¥–µ—Ä–∞
 * –£–ø—Ä–∞–≤–ª—è–µ—Ç —Ä–∞–±–æ—Ç–æ–π —Å–ª–∞–π–¥–µ—Ä–∞ –Ω–∞ –≥–ª–∞–≤–Ω–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü–µ
 */

import { devLog } from '../config.js';

export class Slider {
    constructor() {
        this.slider = document.querySelector('.slider');
        this.slides = document.querySelectorAll('.slider__slide');
        this.indicators = document.querySelectorAll('.slider__indicator');
        this.prevBtn = document.querySelector('.slider__arrow--prev');
        this.nextBtn = document.querySelector('.slider__arrow--next');
        
        this.currentSlide = 0;
        this.slideCount = this.slides.length;
        this.autoplayInterval = null;
        this.autoplayDelay = 5000; // 5 —Å–µ–∫—É–Ω–¥
        
        if (this.slider) {
            this.init();
        }
    }

    init() {
        devLog('Slider: –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è —Å–ª–∞–π–¥–µ—Ä–∞');
        
        // –ü—Ä–æ–≤–µ—Ä—è–µ–º –Ω–∞–ª–∏—á–∏–µ —ç–ª–µ–º–µ–Ω—Ç–æ–≤
        if (!this.slides.length) {
            console.error('Slider: –ù–µ –Ω–∞–π–¥–µ–Ω—ã —Å–ª–∞–π–¥—ã');
            return;
        }
        
        if (!this.indicators.length) {
            console.error('Slider: –ù–µ –Ω–∞–π–¥–µ–Ω—ã –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä—ã');
            return;
        }
        
        console.log(`Slider: –ù–∞–π–¥–µ–Ω–æ ${this.slides.length} —Å–ª–∞–π–¥–æ–≤ –∏ ${this.indicators.length} –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä–æ–≤`);
        
        // –ü—Ä–∏–≤—è–∑—ã–≤–∞–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ —Å–æ–±—ã—Ç–∏–π
        this.bindEvents();
        
        // –ó–∞–ø—É—Å–∫–∞–µ–º –∞–≤—Ç–æ–ø—Ä–æ–∫—Ä—É—Ç–∫—É
        this.startAutoplay();
        
        // –û–±–Ω–æ–≤–ª—è–µ–º –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä—ã
        this.updateIndicators();
    }

    bindEvents() {
        // –ö–Ω–æ–ø–∫–∏ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
        if (this.prevBtn) {
            this.prevBtn.addEventListener('click', () => this.goToPrevSlide());
        }
        
        if (this.nextBtn) {
            this.nextBtn.addEventListener('click', () => this.goToNextSlide());
        }

        // –ò–Ω–¥–∏–∫–∞—Ç–æ—Ä—ã
        this.indicators.forEach((indicator, index) => {
            indicator.addEventListener('click', () => this.goToSlide(index));
        });

        // –ö–ª–∞–≤–∏–∞—Ç—É—Ä–Ω–∞—è –Ω–∞–≤–∏–≥–∞—Ü–∏—è
        document.addEventListener('keydown', (e) => {
            if (e.key === 'ArrowLeft') {
                this.goToPrevSlide();
            } else if (e.key === 'ArrowRight') {
                this.goToNextSlide();
            }
        });

        // –ü–∞—É–∑–∞ –∞–≤—Ç–æ–ø—Ä–æ–∫—Ä—É—Ç–∫–∏ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏
        this.slider.addEventListener('mouseenter', () => this.stopAutoplay());
        this.slider.addEventListener('mouseleave', () => this.startAutoplay());

        // –ü–∞—É–∑–∞ –∞–≤—Ç–æ–ø—Ä–æ–∫—Ä—É—Ç–∫–∏ –ø—Ä–∏ —Ñ–æ–∫—É—Å–µ
        this.slider.addEventListener('focusin', () => this.stopAutoplay());
        this.slider.addEventListener('focusout', () => this.startAutoplay());
    }

    goToSlide(index) {
        if (index === this.currentSlide) return;
        
        // –£–±–∏—Ä–∞–µ–º –∞–∫—Ç–∏–≤–Ω—ã–π –∫–ª–∞—Å—Å —Å —Ç–µ–∫—É—â–µ–≥–æ —Å–ª–∞–π–¥–∞
        this.slides[this.currentSlide].classList.remove('slider__slide--active');
        
        // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –Ω–æ–≤—ã–π –∞–∫—Ç–∏–≤–Ω—ã–π —Å–ª–∞–π–¥
        this.currentSlide = index;
        this.slides[this.currentSlide].classList.add('slider__slide--active');
        
        // –û–±–Ω–æ–≤–ª—è–µ–º –∏–Ω–¥–∏–∫–∞—Ç–æ—Ä—ã
        this.updateIndicators();
        
        devLog(`Slider: –ü–µ—Ä–µ—Ö–æ–¥ –∫ —Å–ª–∞–π–¥—É ${index + 1}`);
    }

    goToNextSlide() {
        const nextIndex = (this.currentSlide + 1) % this.slideCount;
        this.goToSlide(nextIndex);
    }

    goToPrevSlide() {
        const prevIndex = (this.currentSlide - 1 + this.slideCount) % this.slideCount;
        this.goToSlide(prevIndex);
    }

    updateIndicators() {
        this.indicators.forEach((indicator, index) => {
            indicator.classList.toggle('slider__indicator--active', index === this.currentSlide);
        });
    }

    startAutoplay() {
        this.stopAutoplay();
        this.autoplayInterval = setInterval(() => {
            this.goToNextSlide();
        }, this.autoplayDelay);
    }

    stopAutoplay() {
        if (this.autoplayInterval) {
            clearInterval(this.autoplayInterval);
            this.autoplayInterval = null;
        }
    }

    // –ú–µ—Ç–æ–¥—ã –¥–ª—è –≤–Ω–µ—à–Ω–µ–≥–æ —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è
    play() {
        this.startAutoplay();
    }

    pause() {
        this.stopAutoplay();
    }

    destroy() {
        this.stopAutoplay();
        // –ó–¥–µ—Å—å –º–æ–∂–Ω–æ –¥–æ–±–∞–≤–∏—Ç—å —É–¥–∞–ª–µ–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤ —Å–æ–±—ã—Ç–∏–π –ø—Ä–∏ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç–∏
    }
}
