/**
 * –ú–æ–¥—É–ª—å –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å –∞–Ω–∞–ª–∏—Ç–∏–∫–æ–π (–Ø–Ω–¥–µ–∫—Å.–ú–µ—Ç—Ä–∏–∫–∞, Google Analytics)
 */
class Analytics {
  constructor() {
    this.config = {
      enabled: false,
      yandexMetrikaId: null,
      googleAnalyticsId: null,
      debug: false
    };
    
    this.isLocalhost = this.checkLocalhost();
  }

  /**
   * –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ localhost
   */
  checkLocalhost() {
    const hostname = window.location.hostname;
    const port = window.location.port;
    
    return hostname === 'localhost' || 
           hostname === '127.0.0.1' || 
           hostname.includes('192.168') ||
           port === '3000' ||
           hostname.includes('.local');
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –≤—Å–µ—Ö —Å—á–µ—Ç—á–∏–∫–æ–≤
   */
  init(config = {}) {
    this.config = { ...this.config, ...config };
    
    // –û—Ç–∫–ª—é—á–∞–µ–º –∞–Ω–∞–ª–∏—Ç–∏–∫—É –Ω–∞ localhost
    if (this.isLocalhost) {
      console.log('üîß Analytics disabled for localhost environment');
      this.config.enabled = false;
      this.config.debug = true;
    }
    
    if (!this.config.enabled) {
      this.initDebugMode();
      return;
    }
    
    if (this.config.yandexMetrikaId) {
      this.initYandexMetrika();
    }
    
    if (this.config.googleAnalyticsId) {
      this.initGoogleAnalytics();
    }
    
    this.initCustomEvents();
    
    if (this.config.debug) {
      console.log('üìä Analytics initialized:', this.config);
    }
  }

  /**
   * –†–µ–∂–∏–º –æ—Ç–ª–∞–¥–∫–∏ –¥–ª—è —Ä–∞–∑—Ä–∞–±–æ—Ç–∫–∏
   */
  initDebugMode() {
    console.log('üîç Analytics Debug Mode Enabled');
    this.initCustomEvents();
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –Ø–Ω–¥–µ–∫—Å.–ú–µ—Ç—Ä–∏–∫–∏
   */
  initYandexMetrika() {
    const id = this.config.yandexMetrikaId;
    
    if (this.isLocalhost) {
      console.log('‚ö†Ô∏è Yandex.Metrika skipped for localhost');
      return;
    }
    
    // –°–æ–∑–¥–∞–µ–º –≥–ª–æ–±–∞–ª—å–Ω—É—é —Ñ—É–Ω–∫—Ü–∏—é –¥–ª—è –º–µ—Ç—Ä–∏–∫–∏
    window.ym = window.ym || function() {
      (window.ym.a = window.ym.a || []).push(arguments);
    };
    window.ym.l = Date.now();

    // –ó–∞–≥—Ä—É–∂–∞–µ–º —Å–∫—Ä–∏–ø—Ç –º–µ—Ç—Ä–∏–∫–∏
    const script = document.createElement('script');
    script.async = true;
    script.src = 'https://mc.yandex.ru/metrika/tag.js';
    document.head.appendChild(script);

    // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º —Å—á–µ—Ç—á–∏–∫
    script.onload = () => {
      try {
        window.ym(id, 'init', {
          clickmap: true,
          trackLinks: true,
          accurateTrackBounce: true,
          webvisor: true,
          trackHash: true,
          ecommerce: "dataLayer"
        });
        
        console.log('‚úÖ Yandex.Metrika initialized with ID:', id);
      } catch (e) {
        console.error('‚ùå Yandex.Metrika initialization error:', e);
      }
    };

    // –î–æ–±–∞–≤–ª—è–µ–º noscript —Ç–µ–≥
    const noscript = document.createElement('noscript');
    noscript.innerHTML = `<div><img src="https://mc.yandex.ru/watch/${id}" style="position:absolute; left:-9999px;" alt="" /></div>`;
    document.body.appendChild(noscript);
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è Google Analytics
   */
  initGoogleAnalytics() {
    const id = this.config.googleAnalyticsId;
    
    if (this.isLocalhost) {
      console.log('‚ö†Ô∏è Google Analytics skipped for localhost');
      return;
    }
    
    // –ó–∞–≥—Ä—É–∂–∞–µ–º gtag
    const script1 = document.createElement('script');
    script1.async = true;
    script1.src = `https://www.googletagmanager.com/gtag/js?id=${id}`;
    document.head.appendChild(script1);

    // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º gtag
    window.dataLayer = window.dataLayer || [];
    function gtag() { dataLayer.push(arguments); }
    window.gtag = gtag;
    gtag('js', new Date());
    gtag('config', id, {
      page_title: document.title,
      page_location: window.location.href
    });
    
    console.log('‚úÖ Google Analytics initialized with ID:', id);
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –∫–∞—Å—Ç–æ–º–Ω—ã—Ö —Å–æ–±—ã—Ç–∏–π
   */
  initCustomEvents() {
    // –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –∫–ª–∏–∫–æ–≤ –ø–æ –∫–Ω–æ–ø–∫–∞–º
    this.trackButtons();
    
    // –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –≥–∞–ª–µ—Ä–µ–π
    this.trackGalleryViews();
    
    // –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –∫–æ–Ω—Ç–∞–∫—Ç–Ω—ã—Ö —Ñ–æ—Ä–º
    this.trackContactForms();
    
    // –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
    this.trackTimeOnPage();
  }

  /**
   * –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –∫–ª–∏–∫–æ–≤ –ø–æ –∫–Ω–æ–ø–∫–∞–º
   */
  trackButtons() {
    document.addEventListener('click', (e) => {
      const button = e.target.closest('.btn, .button, [role="button"]');
      if (button) {
        const buttonText = button.textContent.trim();
        const buttonClass = button.className;
        
        this.trackEvent('button_click', {
          button_text: buttonText,
          button_class: buttonClass,
          page_url: window.location.href
        });
      }
    });
  }

  /**
   * –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –ø—Ä–æ—Å–º–æ—Ç—Ä–æ–≤ –≥–∞–ª–µ—Ä–µ–π
   */
  trackGalleryViews() {
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        mutation.addedNodes.forEach((node) => {
          if (node.nodeType === 1 && node.matches && node.matches('.photo-gallery__item')) {
            const galleryName = this.getGalleryName();
            this.trackEvent('gallery_view', {
              gallery_name: galleryName,
              page_url: window.location.href
            });
          }
        });
      });
    });

    const galleryContainer = document.querySelector('.photo-gallery__grid');
    if (galleryContainer) {
      observer.observe(galleryContainer, { childList: true, subtree: true });
    }
  }

  /**
   * –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –∫–æ–Ω—Ç–∞–∫—Ç–Ω—ã—Ö —Ñ–æ—Ä–º
   */
  trackContactForms() {
    document.addEventListener('submit', (e) => {
      const form = e.target;
      if (form.matches && form.matches('form')) {
        this.trackEvent('form_submit', {
          form_name: form.name || 'contact_form',
          page_url: window.location.href
        });
      }
    });
  }

  /**
   * –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –≤—Ä–µ–º–µ–Ω–∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
   */
  trackTimeOnPage() {
    let startTime = Date.now();
    let isActive = true;

    const events = ['mousedown', 'mousemove', 'keypress', 'scroll', 'touchstart'];
    events.forEach(event => {
      document.addEventListener(event, () => {
        isActive = true;
      }, { passive: true });
    });

    window.addEventListener('beforeunload', () => {
      if (isActive) {
        const timeOnPage = Math.round((Date.now() - startTime) / 1000);
        this.trackEvent('time_on_page', {
          duration: timeOnPage,
          page_url: window.location.href
        });
      }
    });

    setInterval(() => {
      if (isActive) {
        isActive = false;
        const timeOnPage = Math.round((Date.now() - startTime) / 1000);
        this.trackEvent('page_engagement', {
          duration: timeOnPage,
          page_url: window.location.href
        });
      }
    }, 30000);
  }

  /**
   * –û—Ç–ø—Ä–∞–≤–∫–∞ –∫–∞—Å—Ç–æ–º–Ω–æ–≥–æ —Å–æ–±—ã—Ç–∏—è
   */
  trackEvent(eventName, parameters = {}) {
    if (!this.config.enabled) {
      if (this.config.debug) {
        console.log(`üìà [Debug] Event: ${eventName}`, parameters);
      }
      return;
    }

    // –Ø–Ω–¥–µ–∫—Å.–ú–µ—Ç—Ä–∏–∫–∞
    if (this.config.yandexMetrikaId && window.ym) {
      try {
        window.ym(this.config.yandexMetrikaId, 'reachGoal', eventName, parameters);
      } catch (e) {
        console.error('Yandex.Metrika track error:', e);
      }
    }

    // Google Analytics
    if (this.config.googleAnalyticsId && window.gtag) {
      try {
        window.gtag('event', eventName, parameters);
      } catch (e) {
        console.error('Google Analytics track error:', e);
      }
    }

    if (this.config.debug) {
      console.log('üìä Analytics Event:', eventName, parameters);
    }
  }

  /**
   * –ü–æ–ª—É—á–µ–Ω–∏–µ –∏–º–µ–Ω–∏ –≥–∞–ª–µ—Ä–µ–∏ —Å —Ç–µ–∫—É—â–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã
   */
  getGalleryName() {
    const title = document.querySelector('h1');
    if (title) {
      return title.textContent.trim();
    }
    
    const albumId = document.querySelector('[data-album-id]');
    if (albumId) {
      return albumId.getAttribute('data-album-id');
    }
    
    return 'unknown_gallery';
  }

  /**
   * –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –ø—Ä–æ—Å–º–æ—Ç—Ä–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤ –≥–∞–ª–µ—Ä–µ–µ
   */
  trackImageView(imageName, galleryName) {
    this.trackEvent('image_view', {
      image_name: imageName,
      gallery_name: galleryName,
      page_url: window.location.href
    });
  }

  /**
   * –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –∫–ª–∏–∫–∞ –ø–æ —Ç–µ–ª–µ—Ñ–æ–Ω—É/email
   */
  trackContact(contactType, contactValue) {
    this.trackEvent('contact_click', {
      contact_type: contactType,
      contact_value: contactValue,
      page_url: window.location.href
    });
  }
}

// –°–æ–∑–¥–∞–µ–º –≥–ª–æ–±–∞–ª—å–Ω—ã–π —ç–∫–∑–µ–º–ø–ª—è—Ä
window.Analytics = new Analytics();
