/**
 * –ú–æ–¥—É–ª—å –∞–Ω–∏–º–∞—Ü–∏–π
 * –£–ø—Ä–∞–≤–ª—è–µ—Ç –∞–Ω–∏–º–∞—Ü–∏—è–º–∏ —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
 */

import { devLog } from '../config.js';

export class Animations {
    constructor() {
        this.animatedElements = document.querySelectorAll('[class*="animate-"], .fade-in, .slide-up, .slide-left, .slide-right, .zoom-in, [class*="delay-"]');
        this.observer = null;
        
        if (this.animatedElements.length > 0) {
            this.init();
        }
    }

    init() {
        devLog('Animations: –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –∞–Ω–∏–º–∞—Ü–∏–π');
        
        // –ü—Ä–∏–º–µ–Ω—è–µ–º –∞–Ω–∏–º–∞—Ü–∏–∏ –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–æ–≤, –∫–æ—Ç–æ—Ä—ã–µ —É–∂–µ –≤–∏–¥–Ω—ã –Ω–∞ —ç–∫—Ä–∞–Ω–µ
        this.applyInitialAnimations();
        
        // –ù–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º –Ω–∞–±–ª—é–¥–∞—Ç–µ–ª—å –¥–ª—è –æ—Å—Ç–∞–ª—å–Ω—ã—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤
        this.setupIntersectionObserver();
        
        // –°—Ç–∞–±–∏–ª–∏–∑–∏—Ä—É–µ–º —Å—Ç—Ä–∞–Ω–∏—Ü—É –ø–æ—Å–ª–µ –∑–∞–≥—Ä—É–∑–∫–∏
        this.stabilizePage();
    }
    
    stabilizePage() {
        // –°—Ä–∞–∑—É –¥–µ–ª–∞–µ–º –≤–∏–¥–∏–º—ã–º–∏ –∫—Ä–∏—Ç–∏—á–µ—Å–∫–∏ –≤–∞–∂–Ω—ã–µ —ç–ª–µ–º–µ–Ω—Ç—ã
        const ctaBlock = document.querySelector('.portfolio-categories__cta');
        if (ctaBlock) {
            ctaBlock.classList.add('animate-in');
        }
        
        // –î–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å —Å—Ç–∞–±–∏–ª–∏–∑–∞—Ü–∏–∏ —á–µ—Ä–µ–∑ 3 —Å–µ–∫—É–Ω–¥—ã
        setTimeout(() => {
            document.body.classList.add('page-loaded');
        }, 3000);
        
        // –ü—Ä–∏–Ω—É–¥–∏—Ç–µ–ª—å–Ω–∞—è —Å—Ç–∞–±–∏–ª–∏–∑–∞—Ü–∏—è —á–µ—Ä–µ–∑ 5 —Å–µ–∫—É–Ω–¥
        setTimeout(() => {
            document.body.classList.add('force-visible');
        }, 5000);
    }
    
    applyInitialAnimations() {
        // –ü—Ä–æ–≤–µ—Ä—è–µ–º —ç–ª–µ–º–µ–Ω—Ç—ã, –∫–æ—Ç–æ—Ä—ã–µ —É–∂–µ –≤–∏–¥–Ω—ã –≤ viewport
        this.animatedElements.forEach(element => {
            const rect = element.getBoundingClientRect();
            const isVisible = rect.top < window.innerHeight && rect.bottom > 0;
            
            if (isVisible) {
                // –ù–µ–±–æ–ª—å—à–∞—è –∑–∞–¥–µ—Ä–∂–∫–∞ –¥–ª—è –ª—É—á—à–µ–≥–æ UX
                setTimeout(() => {
                    this.animateElement(element);
                }, 100);
            }
        });
    }

    setupIntersectionObserver() {
        const options = {
            root: null,
            rootMargin: '0px 0px -50px 0px',
            threshold: 0.1
        };

        this.observer = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    this.animateElement(entry.target);
                }
            });
        }, options);

        // –ù–∞–±–ª—é–¥–∞–µ–º —Ç–æ–ª—å–∫–æ –∑–∞ —ç–ª–µ–º–µ–Ω—Ç–∞–º–∏, –∫–æ—Ç–æ—Ä—ã–µ –µ—â–µ –Ω–µ –∞–Ω–∏–º–∏—Ä–æ–≤–∞–Ω—ã
        this.animatedElements.forEach(element => {
            if (!element.classList.contains('animate-in') && !element.classList.contains('fade-in--visible')) {
                this.observer.observe(element);
            }
        });
    }

    animateElement(element) {
        // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –Ω–µ –∞–Ω–∏–º–∏—Ä–æ–≤–∞–Ω –ª–∏ —É–∂–µ —ç–ª–µ–º–µ–Ω—Ç
        if (element.classList.contains('animate-in') || element.classList.contains('fade-in--visible')) {
            return;
        }
        
        // –î–æ–±–∞–≤–ª—è–µ–º –∫–ª–∞—Å—Å—ã –∞–∫—Ç–∏–≤–∞—Ü–∏–∏ –¥–ª—è —Ä–∞–∑–Ω—ã—Ö —Ç–∏–ø–æ–≤ –∞–Ω–∏–º–∞—Ü–∏–∏
        if (element.classList.contains('fade-in')) {
            element.classList.add('fade-in--visible');
        }
        
        if (element.classList.contains('slide-up') || 
            element.classList.contains('slide-left') || 
            element.classList.contains('slide-right') || 
            element.classList.contains('zoom-in')) {
            element.classList.add('animate-in');
        }
        
        // –î–ª—è –≤—Å–µ—Ö animate- –∫–ª–∞—Å—Å–æ–≤
        if (element.className.includes('animate-fade-in-up') ||
            element.className.includes('animate-fade-in-down') ||
            element.className.includes('animate-fade-in-left') ||
            element.className.includes('animate-fade-in-right') ||
            element.className.includes('animate-')) {
            element.classList.add('animate-in');
        }
        
        devLog('Animations: –ê–Ω–∏–º–∞—Ü–∏—è –∞–∫—Ç–∏–≤–∏—Ä–æ–≤–∞–Ω–∞ –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–∞', element);
        
        // –£–¥–∞–ª—è–µ–º —ç–ª–µ–º–µ–Ω—Ç –∏–∑ –Ω–∞–±–ª—é–¥–µ–Ω–∏—è –ø–æ—Å–ª–µ –Ω–µ–±–æ–ª—å—à–æ–π –∑–∞–¥–µ—Ä–∂–∫–∏
        // –≠—Ç–æ –ø–æ–∑–≤–æ–ª—è–µ—Ç –∞–Ω–∏–º–∞—Ü–∏–∏ –∑–∞–≤–µ—Ä—à–∏—Ç—å—Å—è —Å—Ç–∞–±–∏–ª—å–Ω–æ
        if (this.observer) {
            setTimeout(() => {
                this.observer.unobserve(element);
            }, 1000); // –ó–∞–¥–µ—Ä–∂–∫–∞ –≤ 1 —Å–µ–∫—É–Ω–¥—É –¥–ª—è –∑–∞–≤–µ—Ä—à–µ–Ω–∏—è –∞–Ω–∏–º–∞—Ü–∏–∏
        }
    }
}
