/**
 * –£–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω—ã–π –º–æ–¥—É–ª—å –¥–ª—è —Ä–∞–±–æ—Ç—ã –º–æ–±–∏–ª—å–Ω–æ–≥–æ –º–µ–Ω—é
 * –†–∞–±–æ—Ç–∞–µ—Ç –Ω–∞ –≤—Å–µ—Ö —Å—Ç—Ä–∞–Ω–∏—Ü–∞—Ö —Å–∞–π—Ç–∞
 */

class MobileMenu {
    constructor() {
        console.log('üçî –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –º–æ–±–∏–ª—å–Ω–æ–≥–æ –º–µ–Ω—é...');
        
        this.burger = document.querySelector('.header__burger');
        this.mobileMenu = document.querySelector('.header__mobile-menu');
        this.body = document.body;
        this.isOpen = false;

        console.log('üçî –ù–∞–π–¥–µ–Ω –±—É—Ä–≥–µ—Ä:', !!this.burger);
        console.log('üçî –ù–∞–π–¥–µ–Ω–æ –º–æ–±–∏–ª—å–Ω–æ–µ –º–µ–Ω—é:', !!this.mobileMenu);

        this.init();
    }

    init() {
        if (!this.burger || !this.mobileMenu) {
            console.warn('üçî Mobile menu elements not found');
            return;
        }

        console.log('üçî –ü–æ–¥–∫–ª—é—á–µ–Ω–∏–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–æ–≤...');
        
        this.burger.addEventListener('click', (e) => {
            e.preventDefault();
            e.stopPropagation();
            console.log('üçî –ö–ª–∏–∫ –ø–æ –±—É—Ä–≥–µ—Ä—É');
            this.toggle();
        });
        
        // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –Ω–∞ —Å—Å—ã–ª–∫—É
        const links = this.mobileMenu.querySelectorAll('.header__mobile-nav-link');
        console.log('üçî –ù–∞–π–¥–µ–Ω–æ —Å—Å—ã–ª–æ–∫ –≤ –º–µ–Ω—é:', links.length);
        
        links.forEach(link => {
            link.addEventListener('click', () => {
                console.log('üçî –ö–ª–∏–∫ –ø–æ —Å—Å—ã–ª–∫–µ –≤ –º–µ–Ω—é');
                this.close();
            });
        });

        // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ ESC
        document.addEventListener('keydown', (e) => {
            if (e.key === 'Escape' && this.isOpen) {
                console.log('üçî –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ ESC');
                this.close();
            }
        });

        // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø—Ä–∏ –∫–ª–∏–∫–µ –≤–Ω–µ –º–µ–Ω—é
        document.addEventListener('click', (e) => {
            if (this.isOpen && !e.target.closest('.header')) {
                console.log('üçî –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –≤–Ω–µ –º–µ–Ω—é');
                this.close();
            }
        });

        // –ó–∞–∫—Ä—ã—Ç–∏–µ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ä–∞–∑–º–µ—Ä–∞ –æ–∫–Ω–∞
        window.addEventListener('resize', () => {
            if (window.innerWidth > 768 && this.isOpen) {
                console.log('üçî –ó–∞–∫—Ä—ã—Ç–∏–µ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ä–∞–∑–º–µ—Ä–∞ –æ–∫–Ω–∞');
                this.close();
            }
        });
        
        console.log('üçî –ú–æ–±–∏–ª—å–Ω–æ–µ –º–µ–Ω—é –≥–æ—Ç–æ–≤–æ –∫ —Ä–∞–±–æ—Ç–µ!');
    }

    toggle() {
        console.log('üçî Toggle –≤—ã–∑–≤–∞–Ω, —Å–æ—Å—Ç–æ—è–Ω–∏–µ:', this.isOpen);
        if (this.isOpen) {
            this.close();
        } else {
            this.open();
        }
    }

    open() {
        console.log('üçî –û—Ç–∫—Ä—ã—Ç–∏–µ –º–µ–Ω—é...');
        // –ò—Å–ø–æ–ª—å–∑—É–µ–º –ø—Ä–∞–≤–∏–ª—å–Ω—ã–µ –∫–ª–∞—Å—Å—ã, –∫–æ—Ç–æ—Ä—ã–µ –æ–∂–∏–¥–∞–µ—Ç CSS
        this.burger.classList.add('header__burger--active');
        this.mobileMenu.classList.add('header__mobile-menu--active');
        this.body.classList.add('menu-open'); // –ò–∑–º–µ–Ω–∏–ª–∏ –∫–ª–∞—Å—Å –¥–ª—è –±–ª–æ–∫–∏—Ä–æ–≤–∫–∏ –ø—Ä–æ–∫—Ä—É—Ç–∫–∏
        this.burger.setAttribute('aria-expanded', 'true');
        this.isOpen = true;
        console.log('üçî –ú–µ–Ω—é –æ—Ç–∫—Ä—ã—Ç–æ!');
    }

    close() {
        console.log('üçî –ó–∞–∫—Ä—ã—Ç–∏–µ –º–µ–Ω—é...');
        this.burger.classList.remove('header__burger--active');
        this.mobileMenu.classList.remove('header__mobile-menu--active');
        this.body.classList.remove('menu-open');
        this.burger.setAttribute('aria-expanded', 'false');
        this.isOpen = false;
        console.log('üçî –ú–µ–Ω—é –∑–∞–∫—Ä—ã—Ç–æ!');
    }
}

// –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ DOM
document.addEventListener('DOMContentLoaded', () => {
    console.log('üçî DOM –∑–∞–≥—Ä—É–∂–µ–Ω, –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º –º–æ–±–∏–ª—å–Ω–æ–µ –º–µ–Ω—é...');
    new MobileMenu();
});
